@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
  width: 22%;
  margin-bottom: 50px;
  transition: 0.3s;
  border: 2px solid black;
  border-radius: 5px;
  background-color: black;
}

.card-header {
  text-align: center;
  padding: 5px;
  font-size: 12px;
  color: #fff;
}

.card-image {
  text-align: center;
  width: 230px;
  height: 210px;
  border-radius: 2px;
}
.card:hover {
  transform: scale(1.05);
  box-shadow: 0 0 40px -10px rgba(0, 0, 0, 0.25);
}

@media screen and (max-width: 1200px) {
    .card-image {
      width: 180px;
      height: 160px;
    }
  }

@media screen and (max-width: 1000px) {
  .card {
    width: 40%;
  }
  .card-image {
    width: 150px !important;
    height: 120px;
  }
}

@media screen and (max-width: 620px) {
  .container {
    width: 100%;
  }

  .card {
    width: 80%;
  }
  .card-image {
    width: 200px;
    height: 180px;
  }
}

 * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  
  .container {
    width: 90%;
    margin: 50px auto;
  }
  
  .row {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-flow: wrap;
  }
.header-content{
    background-color:black !important;
    font-family: fantasy;
    font-size: 20px;
    font-weight: bold;
    font-style: oblique;
    letter-spacing: 1px;
    width: 100%;
    height: 12vh;
    text-align: center;
}
.box-content{
    width: 100%;
    display: inline-block;
}
.marvel{
    width: 400px;
    height: 50px;
    margin-top: 15px;
}
.content {
    position: relative;
}
.input{
    margin: 18px 0;
    padding: 12px;
    border-radius: 5px;
    border: 0;
    outline: none; 
} 
.App {
  text-align: center;
}
body{
  background: rgb(2,0,36);
background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(255,1,1,1) 53%, rgba(0,0,0,1) 100%, rgba(0,212,255,1) 100%);
}

